@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;padding:0}.Clevernote{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;justify-items:center;height:100vh}.empty-state{height:50px;display:-webkit-flex;display:flex;color:#333;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;text-transform:uppercase;font-size:14px;font-weight:400}

:root{--primary-color: #3a4052}*{box-sizing:border-box;margin:0;padding:0}body{font-family:'Open Sans', sans-serif;line-height:1.5}a{text-decoration:none;color:#3a4052;color:var(--primary-color)}h1{font-weight:300;font-size:60px;line-height:1.2;margin-bottom:15px}.showcase{height:100vh;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;text-align:center;color:#fff;padding:0 20px}.video-container{position:absolute;top:0;left:0;width:100%;height:100%;overflow:hidden}.video-container video{min-width:100%;min-height:100%;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);object-fit:cover}.video-container:after{content:'';z-index:1;height:100%;width:100%;top:0;left:0;background:rgba(0,0,0,0.5);position:absolute}.content{z-index:2}.btn{display:inline-block;padding:10px 30px;background:#3a4052;background:var(--primary-color);color:#fff;border-radius:5px;border:solid #fff 1px;margin-top:25px;opacity:0.7}.btn:hover{-webkit-transform:scale(0.98);transform:scale(0.98)}#about{padding:40px;text-align:center}#about p{font-size:1.2rem;max-width:600px;margin:auto}#about h2{margin:30px 0;color:#3a4052;color:var(--primary-color)}

.sidenavbar{height:100%;width:240px;background:#1a1a1a;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;color:#ccc;isolation:isolate}.sidenavbar .sidenavbar-top{-webkit-flex:1 1;flex:1 1}.sidenavbar .sidenavbar-top .sidenavbar-top__profile{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;padding:15px}.sidenavbar .sidenavbar-top .sidenavbar-top__profile .profile-icon{background:#f8cbad;width:30px;height:30px;border-radius:50%;color:#000;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.sidenavbar .sidenavbar-top .sidenavbar-top__profile .profile-title{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-flex:1 1;flex:1 1;margin-left:10px}.sidenavbar .sidenavbar-top .sidenavbar-top__profile .profile-title .icon{margin:0 10px;font-size:12px}.sidenavbar .sidenavbar-top .sidenavbar-top__search{padding:15px}.sidenavbar .sidenavbar-top .sidenavbar-top__search .search-block{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;background:#404040;padding:8px 10px;border-radius:50px}.sidenavbar .sidenavbar-top .sidenavbar-top__search .search-block .icon{font-size:12px}.sidenavbar .sidenavbar-top .sidenavbar-top__search .search-block input{color:#ffffff;margin-left:10px;background:none;outline:none;border:none}.sidenavbar .sidenavbar-top .sidenavbar-top__create-note{padding:0 15px;color:#ffffff}.sidenavbar .sidenavbar-top .sidenavbar-top__create-note .create-note-btn{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;background:#03a82d;padding:8px 10px;border-radius:50px;cursor:pointer}.sidenavbar .sidenavbar-top .sidenavbar-top__create-note .create-note-btn .icon{font-size:12px}.sidenavbar .sidenavbar-top .sidenavbar-top__create-note .create-note-btn .title{margin-left:10px}.sidenavbar .sidenavbar-top .sidenavbar-top__menu-item{margin-top:10px}.sidenavbar .sidenavbar-top .sidenavbar-top__menu-item ul li a{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;padding:10px 18px;color:#ccc;text-decoration:none}.sidenavbar .sidenavbar-top .sidenavbar-top__menu-item ul li a .icon{font-size:14px;margin-right:8px}.sidenavbar .sidenavbar-top .sidenavbar-top__menu-item ul li a:hover{cursor:pointer;background:#404040}.sidenavbar .sidenavbar-top .sidenavbar-top__menu-item ul li a:active{background:#000000}.sidenavbar .sidenavbar-bottom{padding:8px 15px;border-top:1px solid #555}.sidenavbar .sidenavbar-bottom .sidenavbar-bottom__need-help{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;padding:8px}.sidenavbar .sidenavbar-bottom .sidenavbar-bottom__need-help .icon{font-size:14px;margin-right:8px}

.note-list{height:100%;width:380px;max-width:670px;min-width:196px;background:#f8f8f8;border-right:1px solid #e6e6e6;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.note-list .note-list__header{padding:5px 25px;display:-webkit-flex;display:flex;-webkit-align-items:flex-start;align-items:flex-start;-webkit-flex-direction:column;flex-direction:column;height:100px;box-sizing:border-box;border-bottom:1px solid #ccc}.note-list .note-list__header .note-list__header-title{width:100%;-webkit-flex:1 1;flex:1 1}.note-list .note-list__header .note-list__header-title h1{margin:15px 0 20px;font-weight:500;font-size:20px}.note-list .note-list__header .note-list__header-sub-head{width:100%;margin:5px;font-size:14px}.note-list .note-list__body{-webkit-flex:1 1;flex:1 1;overflow:auto}.note-list .note-list__body .note-card{height:120px;padding:20px 25px;box-sizing:border-box;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;border:1px solid #e6e6e6;text-decoration:none}.note-list .note-list__body .note-card .note-card__head{width:100%;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column;-webkit-flex:1 1;flex:1 1;font-size:14px}.note-list .note-list__body .note-card .note-card__head .note-card__title{width:100%;color:#333;font-weight:600;white-space:nowrap;overflow:hidden;text-overflow:ellipsis}.note-list .note-list__body .note-card .note-card__head .note-card__desc{width:100%;color:#000000;margin-top:5px;white-space:nowrap;overflow:hidden;text-overflow:ellipsis}.note-list .note-list__body .note-card .note-card__date{color:#4d4d4d;font-size:13px}.note-list .note-list__body .note-card:hover{cursor:pointer;background-color:#ffff}.note-list .note-list__body .note-card:active{background-color:#ffff}

.note{display:-webkit-flex;display:flex;-webkit-flex:1 1;flex:1 1;-webkit-flex-direction:column;flex-direction:column;height:100%}.note .note__header{height:100px;width:100%;display:-webkit-flex;display:flex;-webkit-align-items:flex-end;align-items:flex-end;-webkit-justify-content:space-between;justify-content:space-between;padding:20px;box-sizing:border-box;border-bottom:1px solid #e6e6e6}.note .note__header .note__header-date{font-size:14px;color:#737373}.note .note__header .note__header-action-btn{display:-webkit-flex;display:flex}.note .note__header .note__header-action-btn .action-btn{color:#737373;font-size:14px;opacity:0.6;margin:0 10px}.note .note__header .note__header-action-btn .action-btn:hover{opacity:1;cursor:pointer}.note .note__body{-webkit-flex:1 1;flex:1 1;padding:20px}.note .note__body .note__body-head input{padding:10px;font-size:30px;border:none;outline:none;width:100%}.note .note__body .note__body-content textarea{width:100%;height:100px;padding:10px;font-size:16px;border:none;outline:none}

